import React from "react";
import PropTypes from "prop-types";
import classnames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Checkbox from "@material-ui/core/Checkbox";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import Table from "@material-ui/core/Table";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
// @material-ui/icons
import Edit from "@material-ui/icons/Edit";
import Close from "@material-ui/icons/Close";
import Check from "@material-ui/icons/Check";
// core components
import styles from "assets/jss/material-dashboard-react/components/dayslimitStyle.js";
import graphImage from "assets/img/graph.png";
import komodoImage from "assets/img/komodo-logo.png";
import solanaImage from "assets/img/solana-logo.png";

const useStyles = makeStyles(styles);

export default function Dayslimit(props) {
  const classes = useStyles();
  const { salevalue, volumevalue, solovalue } = props;

  return (
    <div className="top-tab-item-waraper">
      <div className="top-tab-content">
        <div className="detail-logo">
          <img src={graphImage} width={80} height={80}></img>
        </div>
        <div className="detail-info">
          <div className="detail-title">Total Sales</div>
          <div className="detail-content">{salevalue}</div>
        </div>
      </div>
      <div className="top-tab-content">
        <div className="detail-logo">
          <img src={komodoImage} width={80} height={80}></img>
        </div>
        <div className="detail-info">
          <div className="detail-title">Total Volume (SOL)</div>
          <div className="detail-content">{volumevalue}</div>
        </div>
      </div>
      <div className="top-tab-content">
        <div className="detail-logo">
          <img src={solanaImage} width={80} height={80}></img>
        </div>
        <div className="detail-info">
          <div className="detail-title">Komodo Sold</div>
          <div className="detail-content">{solovalue}</div>
        </div>
      </div>
    </div>
  );
}

Dayslimit.propTypes = {
  dayslimitIndexes: PropTypes.arrayOf(PropTypes.number),
  dayslimit: PropTypes.arrayOf(PropTypes.node),
  rtlActive: PropTypes.bool,
  checkedIndexes: PropTypes.array,
};
