const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
  container: {
    padding: "30px 30px 0 30px",
  },
  marginTopTitle:{
    marginTop: 20,
    marginBottom: 15
  },
  nftCount:{
    fontSize: 24,
    color: "#ffffff"
  },
  ordered:{
    border: "3px solid #ffffff",
    color: "#ffffff",
    paddingLeft: 10,
  },
  orderSelect:{
    justifyContent: "end",
    marginBottom: "15px !important" 
  },
  sortLabel:{
    fontSize: 17,
    color: "#ffffff"
  },
  completed: {
    display: 'inline-block',
  },
  instructions: {
    marginTop: 1,
    marginBottom: 1,
  },
  filterSelect: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "end",
    alignItems: 'center',
  },
  marginTopResult: {
    marginTop: 14
  }
};

const useStyles = makeStyles(styles);

export default function Marketplace() {
  const classes = useStyles();
  const [age, setAge] = React.useState(10);
  const [startday, setStartday] = React.useState(0);
  const [endday, setEndday] = React.useState(30);
  const [active, setActive] = React.useState(true);
  const [activeStep, setActiveStep] = React.useState(0);
  const [completed, setCompleted] = React.useState({});

  const handleChange = (event) => {
    setAge(event.target.value);
  };

  const handleStep = (step) => () => {
    setActiveStep(step);
  };

  const steps = ['Any', '0', '1', '2', '3'];
  
  const handleClick = () => {
    console.log("adfasdfsadfsa")
    setActive(!active);
  };

  return (
    <div>
      <div className="filter-area">
        <div className="filter-setting">
          <div className="filter-title">
            <span>Filter</span>
          </div>
          <div className="filter-class">
            <span>Class</span>
            <div className="class-buttons">
              <CustomFilterButton title={"Lizard"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Chameleon"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Komodo"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Dragon"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
            </div>
          </div>
          <div className="filter-element">
            <span>Element</span>
            <div className="element-buttons">
              <CustomFilterButton title={"Fire"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Water"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Earth"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Wind"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Dark"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
              <CustomFilterButton title={"Light"} active={active} onClick={()=>handleClick()}></CustomFilterButton>
            </div>
          </div>
          <div className="filter-breed">
            <span>Breed Count</span>
            <div className="breed-element">
                <Stepper alternativeLabel nonLinear activeStep={activeStep}>
                    {steps.map((label, index) => {
                        const stepProps = {};
                        const buttonProps = {};
                        return (
                            <Step key={index} {...stepProps}>
                                <StepButton
                                    onClick={handleStep(index)}
                                    {...buttonProps}
                                >
                                {label}
                                </StepButton>
                            </Step>
                        );
                    })}
                </Stepper>
            </div>
          </div>
          <div className="filter-timer">
            <span>Cooldown Timer</span>
            <div className="time-period">
                <input className="filter-input" value={startday}></input> days <span>-</span>  
                <input className="filter-input" value={endday}></input> days
            </div>
          </div>
          <div className="filter-apply">
            <button className="apply-filter">Apply Filter</button>
            <button className="reset-filter">Reset Filter</button>
          </div>
        </div>
        <div className="filter-result">
            <GridContainer className={classes.marginTopTitle}>
                <GridItem xs={6} sm={6} md={6}>
                <span className={classes.nftCount}>2,438 NFTs</span>
                </GridItem>
                <GridItem xs={6} sm={6} md={6} className={classes.orderSelect}>
                    <div className={classes.filterSelect}>
                        <span className={classes.sortLabel}>Sort By&nbsp;:&nbsp;&nbsp;</span> 
                        <Select
                            value={age}
                            onChange={handleChange}
                            className={classes.ordered}
                            >
                            <MenuItem value={10}>Price: High to Low</MenuItem>
                            <MenuItem value={20}>Price: Low to High</MenuItem>
                            <MenuItem value={30}>Price: Low to High</MenuItem>
                        </Select>
                    </div>
                </GridItem>
            </GridContainer>
            <div  className={classes.marginTopResult}>
                <GridContainer>
                    <GridItem xs={3} sm={3} md={3}>
                    <CustomNFTItem countId={9096} imageUrl={""} breedCount={3} solPrice={16} usdPrice={169}></CustomNFTItem>
                    </GridItem>
                    <GridItem xs={3} sm={3} md={3}>
                    <CustomNFTItem countId={6548} imageUrl={""} breedCount={0} solPrice={21} usdPrice={135}></CustomNFTItem>
                    </GridItem>
                    <GridItem xs={3} sm={3} md={3}>
                    <CustomNFTItem countId={1535} imageUrl={""} breedCount={1} solPrice={9.5} usdPrice={261}></CustomNFTItem>
                    </GridItem>
                    <GridItem xs={3} sm={3} md={3}>
                    <CustomNFTItem countId={1548} imageUrl={""} breedCount={0} solPrice={15} usdPrice={131.5}></CustomNFTItem>
                    </GridItem>
            </GridContainer>
            </div>
          {/* <Pagination count={10} variant="outlined" shape="rounded" /> */}
        </div>
      </div>
    </div>
  );
}
