import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import NFTImage from "assets/img/nft-item.png";
import xsSolana from "assets/img/xs-solana.svg";
import Dinosaur from "assets/img/items/1.png";
import Box from "assets/img/items/2.png";
import Mask from "assets/img/items/3.png";

const useStyles = makeStyles({
  root: {
    width: "100%",
    backgroundColor: "#414141",
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
    color: "#ffffff",
  },
  pos: {
    marginBottom: 12,
    fontSize: 12,
    color: "#ffffff"
  },
  padding10: {
    padding: "10px !important",
  },
  imageItem: {
    width: "100%"
  },
  solImage:{
    width: 60,
    height: 60,
    borderRadius: "10%",
    background: "#00E309",
  },
  usdPrice: {
    fontSize: 12,
    color: "#AAAAAA",
    right: 0,
    position: "absolute"
  },
  solPrice: {
    fontSize: 12,
    color:  "#FFFFFF",
    marginLeft: 5
  },
  priceBar: {
    position: "relative",
    display: "flex",
    alignItems: "center",
  },
  sellBtn: {
    margin: "auto",
    width: "80%",
    marginTop: "30px",
    marginLeft: "10%"
  }
});

const imgStyle = [
  {
    src:Dinosaur
  },
  {
    src:Box
  },
  {
    src:Mask
  },
  {
    src:Dinosaur
  },
]
export default function CustomNFTItem() {
  const classes = useStyles();
  const bull = <span className={classes.bullet}>•</span>;

  return (
    <Card className={classes.root}>
      <CardContent className={classes.padding10}>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
            Komoverse #9950
        </Typography>
        <img src={NFTImage} className={classes.imageItem}></img>
        <Typography className={classes.pos} color="textSecondary">
            <span className='ft-20'> Breed Count: 0</span>
        </Typography>
        <Typography variant="body2" component="div" className={classes.priceBar + " subimages"}>

          {imgStyle.map((prop, key) => {
              return (
                <img src={prop.src} className={classes.solImage} width={50} height={50}></img>
              );
            })}
        </Typography>
        <button className= {classes.sellBtn + " download-button bold"}>SELL</button>
      </CardContent>
    </Card>
  );
}
