import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import NFTImage from "assets/img/nft-item.png";
import xsSolana from "assets/img/xs-solana.svg";

const useStyles = makeStyles({
  root: {
    width: "100%",
    backgroundColor: "#414141",
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
    color: "#ffffff",
  },
  pos: {
    marginBottom: 12,
    fontSize: 12,
    color: "#ffffff"
  },
  padding10: {
    padding: "10px !important",
  },
  imageItem: {
    width: "100%"
  },
  solImage:{
    width: 18,
    height: 18,
    borderRadius: "50%"
  },
  usdPrice: {
    fontSize: 12,
    color: "#AAAAAA",
    right: 0,
    position: "absolute"
  },
  solPrice: {
    fontSize: 12,
    color:  "#FFFFFF",
    marginLeft: 5
  },
  priceBar: {
    position: "relative",
    display: "flex",
    alignItems: "center",
  }
});

export default function CustomNFTItem(props) {
  const classes = useStyles();
  const { countId, imageUrl, breedCount, solPrice, usdPrice } = props
  return (
    <Card className={classes.root}>
      <CardContent className={classes.padding10}>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
            Komoverse #{countId}
        </Typography>
        <img src={NFTImage} className={classes.imageItem}></img>
        <Typography className={classes.pos} color="textSecondary">
            Breed Count: {breedCount}
        </Typography>
        <Typography variant="body2" component="div" className={classes.priceBar}>
            <img src={xsSolana} className={classes.solImage}></img>
            <span className={classes.solPrice}>{solPrice} SOL</span>
            <span className={classes.usdPrice}>${usdPrice}</span>
        </Typography>
      </CardContent>
    </Card>
  );
}


