import React from "react";
import Img1 from '../../assets/img/img1.png';
import Img2 from '../../assets/img/img2.png';
import Track from '../../assets/img/track.svg';
import Solana from '../../assets/img/solana.svg';
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

let walletType = 'phantom';
let walletAddress = 'nikkokVHPyN1RShygZVUnSZKzsnJ9mT2zcKkiTbkUaP';

const useStyles = makeStyles({
    table: {
      minWidth: 650,
      backgroundColor:'#414141',
      color:'#red',
    },
  });

export const Detail = () => {

    
    return (
        <div className = "flex-col detailcomponent">
            <div className = "flex-row space-between">
                <div className = "flex-col">
                    <div>Class</div>
                    <div className="flex-row seconddetail mt-10"><img src = {Img1} width={140}/>&nbsp;&nbsp;Comodo</div>
                </div>
                <div className = "flex-col">
                    <div>Element</div>
                    <div className="flex-row seconddetail mt-10"><img src = {Img2} width={40}/>&nbsp;&nbsp;Comodo</div>
                </div>
                <div className = "flex-col">
                    <div>Breed Count</div>
                    <div className="seconddetail mt-15">0 / 3</div>
                </div>
                <div className = "flex-col">
                    <div>Cooldown Timer</div>
                    <div className="seconddetail mt-20">3 days</div>
                </div>
            </div>
            <div className = "flex-col mt-20">
                <div className = "flex-row">Owner</div>
                <div className = "flex-row mb-20">
                    <span className="20px mb-10 bold available bold">Aviabee</span> 
                    &nbsp;&nbsp;
                    <span className="16px mt-10 walletaddress" >({walletType}: {walletAddress})</span> 
                </div>
            </div>
            <p><b>Body Parts</b></p>
            
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <div className="flex-row seconddetail mt-10">
                        <div>
                            <img src = {Img1} width={140}/>
                            <div className = "flex-col ml-20">
                                <div>Head</div>
                                <div className="ft-20 mt-10 bold">Komodo (water)</div>
                            </div>
                        </div>
                        <div>
                            <img src = {Img2} width={140}/>
                            <div className = "flex-col ml-20">
                                <div>Body</div>
                                <div className="ft-20 mt-10 bold">Robot</div>
                            </div>
                         </div>
                    </div>
                </GridItem>
            </GridContainer>
            <br/>
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <div className="flex-row seconddetail mt-10">
                        <div>
                            <img src = {Img2} width={140}/>
                            <div className = "flex-col ml-20">
                                <div>Headgear</div>
                                <div className="ft-20 mt-10 bold">Pirate Hat</div>
                            </div>
                        </div>
                        <div>
                            <img src = {Img1} width={140}/>
                            <div className = "flex-col ml-20">
                                <div>Weapon</div>
                                <div className="ft-20 mt-10 bold">Trident</div>
                            </div>
                         </div>
                    </div>
                </GridItem>
            </GridContainer>
            <br/><br/>
        </div>
    )
}

export const Listing = () => {

    const classes = useStyles();

    function createData(name, calories, fat, carbs) {
        return { name, calories, fat, carbs };
      }
      
      const rows = [
        createData('3.82137 SOL', '$149.68', '28 days', 'Aviabee(dffs...23ew3)')
      ];


    return (
        <div className = "flex-col detailcomponent listing-part">
            <img src = {Track} width={20} height={20}/>&nbsp;&nbsp;<span className="ft-20 bold">Listing</span>
            <br/><br/>
            
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                    <TableRow>
                        <TableCell>Price</TableCell>
                        <TableCell >USD Price</TableCell>
                        <TableCell >Expiration</TableCell>
                        <TableCell >From</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                    {rows.map((row) => (
                        <TableRow key={row.name}>
                            <TableCell component="th" scope="row">
                                <img src = {Solana} width={40}/>
                                Solana{row.name}
                            </TableCell>
                            <TableCell >{row.calories}</TableCell>
                            <TableCell >{row.fat}</TableCell>
                            <TableCell >{row.carbs}</TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
                </TableContainer>
        </div>
    )
}

export const Offer = () => {

    const classes = useStyles();

    function createData(price, usd, expire, floor, from) {
        return { price, usd, expire, floor, from };
      }
      
      const rows = [
        createData('3.82137 SOL', '$149.68', '28 days', '11% above', 'Aviabee(dffs...23ew3)'),
        createData('2.3241 SOL', '$94.45', '28 days', '12% above', 'Aviabee(dffs...23ew3)'),
        createData('1.5234 SOL', '$94.41', '28 days', '10% above', 'Aviabee(dffs...23ew3)')
      ];


    return (
        <div className = "flex-col detailcomponent listing-part">
            <img src = {Track} width={20} height={20}/>&nbsp;&nbsp;<span className="ft-20 bold">Listing</span>
            <br/><br/>
            
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                    <TableRow>
                        <TableCell>Price</TableCell>
                        <TableCell >USD Price</TableCell>
                        <TableCell >Expiration</TableCell>
                        <TableCell >Floor Difference</TableCell>
                        <TableCell >From</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                    {rows.map((row) => (
                        <TableRow key={row.price}>
                            <TableCell component="th" scope="row">
                                <img src = {Solana} width={40}/>
                                Solana{row.price}
                            </TableCell>
                            <TableCell >{row.usd}</TableCell>
                            <TableCell >{row.expire}</TableCell>
                            <TableCell >{row.floor}</TableCell>
                            <TableCell >{row.from}</TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
                </TableContainer>
        </div>
    )
}

