import React from "react";
import PropTypes from "prop-types";
import classnames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Checkbox from "@material-ui/core/Checkbox";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import Table from "@material-ui/core/Table";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
// @material-ui/icons
import Edit from "@material-ui/icons/Edit";
import Close from "@material-ui/icons/Close";
import Check from "@material-ui/icons/Check";
// core components
import styles from "assets/jss/material-dashboard-react/components/RecentListedStyle.js";
import komodoImage from "assets/img/komodo.png";
import solanaImage from "assets/img/solana.png";
import diamondImage from "assets/img/diamond.png";

const useStyles = makeStyles(styles);

export default function RecentListItem(props) {
  const classes = useStyles();
  const { content } = props;

  return (
    <div className="recent-list-item">
        <img src={komodoImage} className="detail-logo" width={68} height={68}></img>
        <div className="recent-detail-info">
            <div className="breed-id">#{content.count_id}</div>
            <div className="breed-count">Breed Count: {content.breadcount}</div>
        </div>
        <div className="itemlist-info">
            <div className="itemlist-detail">
                {content.items.map((prop, key) => {
                    return (
                        <img src={solanaImage} width={35} height={35} key={key}></img>
                    );
                })}
            </div>
            <img src={diamondImage} className="diamond" width={33} height={33}></img>
        </div>
        <div className="item-price-info">
            <div className="sol-price-detail">
                <div className="solprice-logo">
                    <img src={solanaImage}></img>
                </div>
                <div className="sol-price">{content.sol_price} SOL</div>
            </div>
            <div className="usd-price">${content.usd_price}</div>
            <div className="time-ago">{content.time_ago} minutes ago</div>
        </div>
        <div className="item-detail-view">
            <svg width="11" height="16" viewBox="0 0 11 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M9.74365 8.64502L3.10303 15.2856C2.64404 15.7446 1.90186 15.7446 1.44775 15.2856L0.344238 14.1821C-0.114746 13.7231 -0.114746 12.981 0.344238 12.5269L5.05127 7.81982L0.344238 3.11279C-0.114746 2.65381 -0.114746 1.91162 0.344238 1.45752L1.44287 0.344238C1.90186 -0.114746 2.64404 -0.114746 3.09814 0.344238L9.73877 6.98486C10.2026 7.44385 10.2026 8.18604 9.74365 8.64502Z" fill="white"/>
            </svg>
        </div>
    </div>
  );
}

RecentListItem.propTypes = {
  checkedIndexes: PropTypes.array,
};
