import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";

// material-ui components
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
// core components

import styles from "assets/jss/material-dashboard-react/components/customTopTabsStyle.js";

const useStyles = makeStyles(styles);

export default function CustomTopTabs(props) {
  const [value, setValue] = React.useState(0);
  const handleChange = (event, value) => {
    setValue(value);
  };
  const classes = useStyles();
  const { tabs, title } = props;
  return (
    <div className="toptab-bar">
        <Tabs
          value={value}
          onChange={handleChange}
          classes={{
            root: classes.tabsRoot,
            indicator: classes.displayNone,
            scrollButtons: classes.displayNone,
          }}
        >
          {tabs.map((prop, key) => {
            // var icon = {};
            // if (prop.tabIcon) {
            //   <div className="toptab-icon">
            //     <img src={prop.tabIcon} width={24} height={24}></img>
            //   </div>
            // }
            return (
              <Tab
                classes={{
                  root: classes.tabRootButton,
                  selected: classes.tabSelected,
                  wrapper: classes.tabWrapper,
                }}
                key={key}
                label={prop.tabName}
                icon={<img src={prop.tabIcon} width={24} height={24}></img>}
              />
            );
          })}
        </Tabs>
        <div>
          {tabs.map((prop, key) => {
            if (key === value) {
              return <div key={key}>{prop.tabContent}</div>;
            }
            return null;
          })}
        </div>
    </div>
  );
}

CustomTopTabs.propTypes = {
  title: PropTypes.string,
  tabs: PropTypes.arrayOf(
    PropTypes.shape({
      tabName: PropTypes.string.isRequired,
      tabIcon: PropTypes.string,
      tabContent: PropTypes.node.isRequired,
    })
  ),
};
