import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";

import CustomTopTitle from "components/CustomTopTitle/CustomTopTitle.js";
import CustomTopTabs from "components/CustomTopTabs/CustomTopTabs.js";

import cartIcon from '@material-ui/icons/ShoppingCart';
import tabIcon from "assets/img/toptabicon.svg";

import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';

import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";

import Solana from '../../assets/img/solana.svg';
import NFTImg from '../../assets/img/NFTImg.png';
import { Detail, Listing, Offer} from './SingleListingComponent';

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
  container: {
    padding: "30px 30px 0 30px",
  }
};

const useStyles = makeStyles(styles);

export default function SingleListing() {
  const classes = useStyles();
  return (
    <div className='font-white'>
        <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
                <div className="mt-20 mb-20">
                    <ArrowBackIosIcon />
                    <span className="ft-20 bold">Back to Inventory</span>
                </div>
            </GridItem>
            <GridItem xs={12} sm={12} md={5}>
                <button  className="nftname" >Komoverse #8988</button>
            </GridItem>
            <GridItem xs={12} sm={12} md={7}>
                <GridContainer>
                    <GridItem xs={12} sm={12} md={12} lg={6}>
                        <div className="flex-row">
                            <img src = {Solana} />
                            <div className="flex-col ml-20">
                                <span className="listingtext1">3.82137 SOL</span><br/>
                                <span className="listingtext2">$149.68</span>
                            </div>
                        </div>
                        </GridItem>
                    <GridItem xs={12} sm={12} md={12} lg={6}>
                        <div className = 'btn-group'>
                            <button className="offer-button">Make Offer</button>
                            <button className="buy-button">Buy Now</button>
                        </div>
                    </GridItem>
                </GridContainer>
            </GridItem>
            <div className="leftImage mt-20">
                <GridItem xs={12} sm={12} md={4}>
                        <img src = {NFTImg} width={500} />
                </GridItem>
                <GridItem xs={12} sm={12} md={7}>
                    <Detail/>
                    
                    <Listing/>
                    
                    <Offer/>
                </GridItem>
            </div>
        </GridContainer>
    </div>
  );
}
